* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;
}


.App {
  text-align: center;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  width: 10%;
  height: 10%;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-wobble infinite 10s alternate;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-wobble { 
  from {
    transform: rotate(-5deg) translateX(-5%);
    filter: hue-rotate(0deg);
  }
  to {
    transform: rotate(5deg) translateX(5%);
    filter: hue-rotate(360deg);
  }
}
