:root {
    --primary: #2284C5;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    text-decoration: none;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #2284C5;
}

.btn--primary {
    background-color: var(--primary)
}

.btn--outline {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--md{
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
}

.btn--lg{
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
}